.thim-sc-counter-box {
  text-align: center;
  .wrapper-counter-box {
    display: inline-block;
    .counter-box-item {
      width: 283px;
      height: 283px;
      background: transparent;
      float: left;
      margin: 0px 57px;
      text-align: center;
      position: relative;
      border: 0;
      padding: 80px 0px;
      &:before {
        content: '';
        position: absolute;
        width: 200px;
        height: 200px;
        @include rotate(-315deg);
        border: 2px solid #fff;
        left: 41px;
        top: 41px;
      }
      &:first-child {
        margin-left: 0;
      }
      &:last-child {
        margin-right: 0;
      }
      .number {
        font-weight: bold;
        font-size: 72px;
        color: #fff;
        line-height: 80px;
        margin-bottom: 4px;
      }
      .text {
        font-weight: 700;
        font-size: 36px;
        color: #fff;
        line-height: 38px;
      }
    }
  }
}