/*
* Style for footer */

footer#colophon {
  .footer-top {
    max-width: 1170px;
    padding-top: 100px;
    padding-bottom: 60px;
    border-bottom: 1px solid transparent;
  }
  .footer {
    padding-top: 53px;
    padding-bottom: 39px;
    &.no-padding {
      padding: 0;
    }
  }
  .thim-footer-location {
    margin-bottom: 17px;
    line-height: 20px;
    padding-top: 8px;
    .info {
      margin-bottom: 13px;
      line-height: 24px;

      .fa {
        width: 30px;
        float: left;
        padding-top: 3px;
      }
      span {
        padding-left: 30px;
        display: block;
      }
    }
  }
  .social-link {
    a {
      &.facebook:hover i {
        color: #314B7C;
        border-color: #314B7C;
      }
      &.twitter:hover i {
        color: #339DC4;
        border-color: #339DC4;
      }
      &.skype:hover i {
        color: #01ABE9;
        border-color: #01ABE9;
      }
      &.pinterest:hover i {
        color: #B31217;
        border-color: #B31217;
      }
      &.google:hover i {
        color: #DC4335;
        border-color: #DC4335;
      }
      &.dribble:hover i {
        color: #EE5188;
        border-color: #EE5188;

      }
      &.linkedin:hover i {
        color: #007BB6;
        border-color: #007BB6;
      }
      &.digg:hover i {
        color: #005F93;
        border-color: #005F93;
      }
      &.youtube:hover i {
        color: #E32C26;
        border-color: #E32C26;
      }
      &.instagram:hover i {
        color: #D95443;
        border-color: #D95443;
      }
    }
  }
  .mc4wp-form .mc4wp-form-fields input {
    height: 50px;
  }

  .widget-title {
   margin-top: 0;
    margin-bottom: 20px;
  }

  .footer-sidebars {
    clear: both;
    .footer-sidebar {
      .widget {
        ul {
          margin: 0;
          li {
            list-style: none;
            line-height: 38px;
          }

          &.menu li {
            @include transition(all .5s cubic-bezier(.215,.61,.355,1));
            &:hover {
              @include translate(10px,0);
            }
          }
        }
      }
    }
    .col-md-20 {
      width: 20%;
    }
  }

  .copyright-content {
    padding: 35px 0;
    border-top: 1px solid transparent;
    ul {
      margin: 0;
      list-style: none;
      li {
        display: inline-block;
      }
    }
  }
}

ul.list-info {
  li {
    .fa {
      float: left;
      margin-top: 3px;
    }
    span {
      margin-left: 30px;
      display: block;
    }
    width: 100%;
    clear: both;
    line-height: 24px !important;
    margin-bottom: 17px;
  }
}