.thim-sc-categories-link {
  .content {
    position: relative;
    overflow: hidden;
    img {
      width: 100%;
      @include transition(all 1s);
    }
    &:hover {
      img {
        @extend .effect-zoom;
      }
    }

    .categories-title {
      position: absolute;
      bottom: 23px;
      left: 30px;
      margin: 0;
      font-size: 24px;
      color: #fff;
      z-index: 1;
      &.has-line {
        padding-bottom: 5px;
        &:after {
          position: absolute;
          content: "";
          width: 30px;
          height: 2px;
          left: 0;
          bottom: 0;
        }
      }

      &.center-bottom {
        left: 50%;
        top: auto;
        @include translate(-50%, -50%);
        &.has-line:after {
          right: 0;
          margin: auto;
        }
      }
      &.center-center {
        top: 50%;
        left: 50%;
        bottom: auto;
        @include translate(-50%, -50%);
        &.has-line:after {
          right: 0;
          margin: auto;
        }
      }
      &.right {
        right: 30px;
        left: auto;
        &.has-line:after {
          right: 0;
          left: auto;
        }
      }
    }
    p {
      margin: 0;
    }
    .categories-link {
      position: absolute;
      width: 100%;
      height: 100%;
      left: 0;
      top: 0;
      bottom: 0;
      right: 0;
      background-color: rgba(0,0,0,.46);
    }

  }
}

