.thim-sc-hb-rooms {
  .tp-hotel-booking {
    &.hb-catalog-column-1 > .hb_room {
      width: 100%;
    }
    &.hb-catalog-column-2 > .hb_room {
      width: 50%;
    }
    &.hb-catalog-column-3 > .hb_room {
      width: 33.33%;
    }
    &.hb-catalog-column-4 > .hb_room {
      width: 25%;
    }
  }
  .link-to-rooms {
    text-align: right;
    padding-bottom: 60px;
    margin-top: -34px;
    a {
      text-decoration: none !important;
      @extend .thim-button;
      i {
        @extend .thim-button-icon;
      }
      &:hover {
        i {
          @extend .effect-right;
        }
      }
    }
  }
}

