@media (max-width: 768px) {
	.tax-hb_room_type,
	.post-type-archive-hb_room {
		&.responsive {
			.rooms.tp-hotel-booking{
				margin-left: -15px;
				margin-right: -15px;
				> .hb_room {
					width: 50%;
					&:nth-child(2n+1){
						clear: both;
					}
					&:nth-child(3n+1){
						clear: none;
					}
				}
			}
		}
	}
}

@media (max-width: 480px) {
	.tax-hb_room_type,
	.post-type-archive-hb_room {
		&.responsive {
			.rooms.tp-hotel-booking{
				margin-left: 0px;
				margin-right: 0px;
				> .hb_room {
					width: 100%;
					padding: 0px;
				}
			}
		}
	}

	.single-hb_room {
		&.responsive {
			.summary {
				.inner-content {
					.title {
						width: 100%;
						margin-bottom: 0px;
					}
				}
			}
			.hb_related_other_room {
				.hb_room {
					margin-bottom: 30px;
					max-width: 100%;
				}
			}
		}
	}
}