// Shortcode Thim Posts
.thim-sc-posts {
  &.site-content {
    padding-top: 0;
    padding-bottom: 0;
    .blog-content {
      width: auto !important;
      height: auto !important;
      article {
        margin-bottom: 10px;
        .entry-content{
          padding: 25px 0 30px;
          &:after {
            display: none;
          }
          .entry-meta {
            margin: 0;
            padding: 0;
          }

          .entry-title {
            font-size: 24px;
            line-height: 34px;
          }
          .entry-summary {
            margin-top: 20px;
          }
          .readmore {
            margin-top: 30px;
          }
        }


        .entry-meta span {
          float: none;
          margin: 0;
        }
      }
    }
  }
}