.thim-sc-list-events {
  padding: 0 40px 40px;
  .content-events {
    .item-event {
      border-bottom: 1px solid $border-color;
      padding: 40px 0;
      &:last-child {
        border: none;
      }
      .time-from {
        width: 40px;
        float: left;
        padding-top: 9px;
        text-align: center;
        font-weight: bold;
        text-transform: uppercase;
        .date {
          font-size: 36px;
          margin-bottom: 5px;
        }
        .month {
          font-size: 14px;
        }
      }

      .event-wrapper {
        width: calc(100% - 65px);
        width: -webkit-calc(100% - 65px);
        width: -moz-calc(100% - 65px);
        padding: 0 25px 0;
        float: right;
        border-left: 1px solid $border-color;
        .title {
          line-height: 34px;
          margin-top: 0;
          margin-bottom: 0;
          font-size: 24px;
        }
      }

      &:after {
        @extend .clear-after;
      }
    }
  }
  .view-all {
    @extend .thim-button;
    i {
      @extend .thim-button-icon;
    }
    &:hover {
      i {
        @extend .effect-right;
      }
    }
  }
}


