/*
 * Style for menu right */

header.site-header .menu-right {
	padding-top: 14px;
	padding-bottom: 10px;

	> div {
		padding: 0 18px;
		position: relative;
		&:after {
			position: absolute;
			top: 0;
			right: 0;
			content: "";
			width: 1px;
			height: 21px;
			background: rgba(255, 255, 255, 0.3);
		}
		&:first-child {
			&:after {
				display: none;
			}
		}
	}

	ul {
		margin: 0;
		&.header-info {
			li {
				position: relative;
				padding: 0 18px;
				&:after {
					position: absolute;
					top: 0;
					right: 0;
					content: "";
					width: 1px;
					height: 21px;
					background: rgba(255, 255, 255, 0.3);
				}
				&:first-child {
					padding-left: 0;
				}
				&:last-child {
					&:after {
						display: none;
					}
					padding-right: 0;
				}
				a {
					font-weight: bold;
					&.btn-book:hover {
						text-decoration: underline;
					}
					i {
						padding-right: 4px;
					}
				}
			}
		}
	}

	.dk-select {
		padding-right: 18px;
		max-height: 20px;
		margin: 0;
		position: relative;
		&:after {
			font-family: FontAwesome;
			content: "\f107";
			position: absolute;
			top: 50%;
			right: 0px;
			@include translate(0, -50%);
		}
		.dk-selected {
			position: relative;
			padding: 0;
			border: 0;
			font-weight: bold;
			background-color: transparent;
			text-transform: uppercase;
			&:after,
			&:before {
				display: none;
			}
		}
		.dk-select-options {
			text-align: left;
			max-height: 9.5em;
			.dk-option {
				color: initial;
				line-height: 40px;
				width: 100%;
				display: block;
			}

			.dk-option-highlight {
				background-color: #eee;
			}
		}

		.dk-select-options {
			width: 60px;
			min-width: 60px;
			background-color: #fff;
			padding: 0;
			li {
				margin: 2px 0;
				font-weight: 400 !important;
				line-height: 30px !important;
				&:first-child {
					margin-top: 0;
				}
				&:last-child {
					margin-bottom: 0;
				}
			}
			&::-webkit-scrollbar {
				width: 3px;
			}
			&::-webkit-scrollbar-track {
				-webkit-box-shadow: inset 0 0 3px rgba(0, 0, 0, 0.3);
				border-radius: 3px;
			}
			&::-webkit-scrollbar-thumb {
				border-radius: 3px;
				-webkit-box-shadow: inset 0 0 3px rgba(0, 0, 0, 0.5);
			}
			.dk-option-selected {
				background: #eee;
			}
		}

	}

	#google_language_translator {
		select.goog-te-combo {
			display: none !important;
		}
		.goog-te-gadget {
			color: inherit !important;
		}
	}

	select {
		font-weight: bold;
	}

	.mobile {
		display: inline-block;
		margin-left: 11px;
		line-height: 30px;
	}

	@media (min-width: 768px) {
		> .widget {
			float: right;
			&:first-child {
				padding-right: 0;
			}
		}
	}
}
