      }
    }

    @media (max-width: 375px) {
      &.woocommerce-page.woocommerce-cart .shop_table tbody td.actions {
        .coupon input.input-text {
          width: 140px !important;
          padding: 0 10px;
        }
        input {
          margin-top: 0;
        }
      }
      .thim-sc-testimonial.left {
        padding: 60px 20px;
        margin-top: -60px;
        .owl-carousel .owl-item {
          min-width: 220px;
          .testimonial-item {
            padding-right: 0;
            .testimonial-content {
              max-width: 215px;
            }
          }
        }
      }
      .thim-sc-text-banner {
        .content-banner {
          .banner-link {
            margin-left: 30px;
          }
          .banner-title {
            padding-left: 30px;
            &:before {
              width: 20px;
              top: 28px !important;
            }
          }

          .banner-title-second {
            padding-left: 30px !important;
          }
        }
      }
      .thim-sc-hb-rooms {
        .link-to-rooms {
          text-align: left;
          padding-bottom: 30px;
          margin-top: 0;
        }
        .tp-hotel-booking > .hb_room {
          width: 100% !important;
        }
      }
      &.creative-left {
        .header_v3 {
          .menu-mobile-effect.navbar-toggle {
            left: 15px !important;
          }
        }
      }
      &.creative-right {
        .header_v3 {
          .menu-mobile-effect.navbar-toggle {
            right: 15px !important;
          }
        }
      }

      #comments .list-comments .comment-list li .content-comment .author .comment-extra-info {
