          }
        }
      }
    }

    @media (max-width: 320px) {
      .thim-sc-list-events {
        padding: 0 25px 25px;
      }
    }
  }

  &.disable-responsive {
    // Reset the container
    .container {
      width: 1170px;
      max-width: none !important;
    }

    .wrapper-container {
      min-width: 1170px !important;
      overflow: visible !important;
    }

    // Css
    .mobile-menu-container,
    .menu-mobile-effect {
      display: none !important;
    }
  }
}