.thim-sc-filter-gallery {
  .wrapper-filter-controls {
    text-align: center;

    .filter-controls {
      display: inline-block;
      padding: 0 10px;
      margin-bottom: 60px;
      h5 {
        display: inline-block;
        margin: 0;
      }

      .filter {
        display: inline-block;
        border-bottom: 2px solid transparent;
        text-transform: uppercase;
        cursor: pointer;
        font-size: 14px;
        font-weight: 700;
        margin: 0 25px;
      }
    }
  }
  .wrapper-gallery-filter {
    .item-photo {
      min-height: 0;
    }
    .inner {
      position: relative;
      overflow: hidden;
      margin-bottom: 30px;
      &:before {
        content: '';
        position: absolute;
        left: 0;
        top: 0;
        right: 0;
        bottom: 0;
        background: rgba(0, 0, 0, .5);
        z-index: 1;
        @include opacity(0);
        @include transition (all 1s);

      }
      &:hover {
        img {
          @extend .effect-zoom;
        }
        .magnific-link:after,
        &:before {
          @include opacity(1);
        }
      }
      .magnific-link {
        img {
          @include transition (all 1s);
        }

        &:after {
          width: 40px;
          height: 40px;
          content: "\f002";
          font: normal normal normal 28px/1 FontAwesome;
          color: #fff;
          position: absolute;
          top: 50%;
          left: 50%;
          text-align: center;
          z-index: 2;
          @include translate(-50%, -50%);
          @include opacity(0);
          @include transition(all .1s ease-in-out);
        }
      }
    }
  }
}