/*
 * Style sidebar blog */

.sidebar-single-image {
  .textwidget {
    position: relative;
    overflow: hidden;
    img {
      width: 100%;
      @include transition(all 1s);
    }
  }
  &:hover {
    .textwidget {
      img {
        @extend .effect-zoom;
      }
    }
  }
}

.widget-area .widget {
  position: relative;
  border: 1px solid $border-color;
  padding: 30px;
  &.widget_search {
    .thim-search-box {
      .toggle-form {
        display: none !important;
        @include opacity(0);
      }

      .form-search-wrapper {
        position: static;
        z-index: 1;
        background: transparent;
        visibility: inherit;
        overflow: inherit;
        height: auto;
        @include scale(1, 1);
        @include opacity(1);

        .background-toggle {
          display: none !important;
          @include opacity(0);
        }
        .search-form {
          top: 0;
          @include translate(0, 0);
          .search-field {
            padding: 7px 15px;
          }
          button {
            width: 40px;
          }
        }
      }
    }
  }
  &:last-child {
    margin-bottom: 0 !important;
  }
  &.no-padding {
    padding: 0 !important;
  }
  .widget-title {
    text-transform: uppercase;
    margin: 0 0 20px;
  }
  &.widget_archive,
  &.widget_categories,
  &.widget_pages,
  &.widget_meta,
  &.widget_recent_comments,
  &.widget_recent_entries,
  &.widget_nav_menu {
    ul li {
      .children,
      .sub-menu {
        padding-left: 15px;
        li {
          border: none;
          padding-bottom: 0;
        }
      }
    }
  }

  ul {
    margin: 0;
    li {
      position: relative;
      margin: 0;
      padding: 10px 0 10px 20px;
      list-style: none;
      line-height: 20px;
      display: block;
      a {
        font-size: $font-size-small;
        text-transform: uppercase;
        font-weight: 700;
      }
      &:before {
        content: '';
        width: 6px;
        height: 6px;
        background: $border-color;
        border-radius: 50%;
        display: inline-block;
        position: absolute;
        left: 0;
        top: 16px;
      }
      ul li ul li ul {
        padding-left: 0 !important;
        li ul li ul li {
          padding-left: 0;
          &:before {
            display: none;
          }
        }
      }
    }
  }

  select {
    max-width: 100%;
    width: 100%;
    height: 40px;
  }
  .dk-select {
    width: 100%;
    height: 40px;
    line-height: 40px;
    .dk-select-options {
      left: 0;
      right: 0;
      li:before {
        display: none;
      }
    }
  }
  &.widget_categories,
  &.widget_archive {
    ul li {
      font-size: 13px;
      font-weight: 700;
    }
  }
  li {
    .rss-date {
      display: block;
    }
    a.rsswidget {
      margin-top: 10px;
      font-size: 15px;
    }
    .rssSummary {
      text-align: left;
    }
  }
  .tagcloud {
    text-align: left;
    a {
      display: inline-block;
      padding: 5px 10px;
      border: 1px solid $border-color;
      font-size: 15px !important;
      margin-bottom: 5px;
    }
  }
  &.widget_recent_comments,
  &.widget_recent_entries {
    ul {
      text-align: left;
    }
  }

  &.widget_nav_menu ul.menu {
    text-align: left;
    li.menu-item-has-children > a {
      &:after {
        font-family: FontAwesome;
        content: "\f107";
        font-size: 13px;
        padding-left: 5px;
        @include opacity(.5);
      }
    }
    ul.sub-menu {
      li {
        padding-right: 0;
      }
    }
  }
}

.theiaStickySidebar > aside:last-child {
  margin-bottom: 0 !important;
}