
.hotel-booking-search {
  padding: 0;
  &.layout-default {
    padding: 0;
    form {
      font-size: 14px;
      text-align: center;
      &:after {
        @extend .clear-after;
      }
      .hb-form-table {
        display: inline-block;
        vertical-align: middle;
        .hb-form-field {
          display: inline-block;
          vertical-align: middle;
          padding: 0;
          margin-bottom: 0;
          float: left;
          //margin: 0 0 0 -3px;
          border-right: 1px solid $border-color;
          &:last-child {
            //border: none;
            //margin: 0 0 0 -4px;
          }
          .hb_input_field {
            display: block;
            background-color: #FFF;
            &:after {
              font-size: 12px;
              top: 0;
              min-width: 15px;
              line-height: 60px;
            }
          }
          input {
            min-width: 150px !important;
            height: 60px;
            padding-left: 20px;
            padding-right: 20px;
            border: none;
            background: transparent;
            z-index: 2;
            @include rounded(0);
            @include placeholder(#4a4a4a);
            &.error {
              @include placeholder(#a00);
            }
          }
          select[name="adults_capacity"] {
            display: none !important;
          }
          .dk-select {
            display: block;
            .dk-selected {
              min-width: 94px;
              text-align: left;
              height: 60px;
              line-height: 60px;
              border: none;
              padding-left: 20px;
              padding-right: 20px;
              &:after {
                display: none;
              }
              &:before {
                content: "\f107";
                font-family: FontAwesome;
                border: none;
                top: 0;
                right: 20px;
                margin: 0;
                line-height: 59px;
                font-weight: bold;
              }
            }
            .dk-select-options {
              left: 0;
              right: 0;
              padding: 0;
              border: none;
              text-align: left;
              .dk-option {
                margin: 0 0 2px;
                padding: 5px 20px;
                &:last-child {
                  margin-bottom: 0;
                }
                &.dk-option-highlight,
                &.dk-option-selected {
                  color: #fff;
                }
              }
            }
          }
        }
      }

      .hb-submit {
        display: inline-block;
        vertical-align: middle;
        padding: 0;
        //margin: 0 0 0 -3px;
        margin-left: -5px;
        button {
          min-width: 180px;
          font-size: 14px;
          font-weight: bold;
          padding-right: 20px;
          padding-left: 20px;
          color: #fff;
          height: 60px;
          border: none;
          text-transform: uppercase;
          @include rounded(0);
          &.hb_loading {
            position: relative;
            overflow: hidden;
            &:after {
              position: absolute;
              content: '';
              display: block;
              background-image: url('assets/images/icons/ajax-loader.gif');
              background-size: 26px 26px;
              background-repeat: no-repeat;
              background-color: rgba(255, 255, 255, 0.75);
              left: 0;
              right: 0;
              top: 0;
              bottom: 0;
              background-position: center center;
              z-index: 9;
            }
          }
        }
      }
    }
  }
  &.layout-box {
    padding: 0;
    form {
      .hb-form-table {
        width: 100%;
        &:after {
          @extend .clear-after;
        }

        .hb-form-field {
          width: 33.33%;
          float: left;
          padding-left: 15px;
          text-align: center;

          &:first-child {
            padding-left: 0;
          }
          .label {
            font-weight: normal;
            margin-top: 0;
          }

          .hb_input_field {
            display: block;
            padding: 40px 20px 80px;
            border: 1px solid #ddd;
            .month {
              font-size: 18px;
              font-weight: bold;
              text-transform: uppercase;
              margin-bottom: 18px;
            }

            &:after {
              display: none;
            }

            .ui-datepicker-trigger {
              position: absolute;
              left: 0;
              bottom: 25px;
              background: transparent;
              border: 0;
              width: 100%;
              min-width: 100%;
              border-radius: 0;
              height: 40px;
              line-height: 40px;
              padding: 0;
              margin-top: 15px;
            }
          }

          .thim-date-picker,
          select[name="adults_capacity"] {
            text-align: center;
            min-width: 20px;
            font-size: 72px;
            font-weight: bold;
            height: auto;
            border: none;
            padding: 0;
            max-width: 81px;
            -webkit-appearance: none;
            -moz-appearance: none;
            @include rounded(0);
            @include box-shadow(none);
            &.error {
              color: #a00;
            }
          }

          .hb-guest-field {
            position: relative;
            display: block;
            padding: 81px 0;
            border: 1px solid #ddd;
            select {
              line-height: 120%;
              pointer-events: none;
            }

            span {
              position: absolute;
              left: 20px;
              right: 20px;
              margin: auto;
              height: 40px;
              line-height: 40px;
              display: block;
              border: none;
              background: none;
              cursor: pointer;
              @include rounded(0);
              @include box-shadow(none);
              &.goUp {
                top: 33px;
              }
              &.goDown {
                bottom: 25px;
                i {
                  display: inline-block;
                  @include rotation(-180deg);
                }
              }
            }
          }
        }

      }
      .hb-submit {
        text-align: right;
        margin-top: 10px;
        button {
          font-weight: 700;
          font-size: 15px;
          display: inline-block;
          padding: 18px 68px;
          text-transform: uppercase;
          color: #fff;
          height: auto;
          border: none;
          @include rounded(0);
          @include box-shadow(none);
          &.hb_loading {
            position: relative;
            overflow: hidden;
            &:after {
              position: absolute;
              content: '';
              display: block;
              background-image: url('assets/images/icons/ajax-loader.gif');
              background-size: 26px 26px;
              background-repeat: no-repeat;
              background-color: rgba(255, 255, 255, 0.75);
              left: 0;
              right: 0;
              top: 0;
              bottom: 0;
              background-position: center center;
              z-index: 9;
            }
          }
        }
      }
    }
  }
}

#ui-datepicker-div {
  border-radius: 0;
  border: 1px solid $border-color;
  z-index: 9999 !important;
  padding: 5px !important;
  min-width: 300px;
  button,
  button:focus,
  select,
  select:focus {
    outline: 0;
    box-shadow: none;
  }
  .ui-timepicker-div {
    select {
      background: #fff !important;
      padding: 4px 0;
    }

    > dl {
      margin-bottom: 0;
      padding-top: 20px;
      &:after {
        display: block;
        content: '';
        clear: both;
      }
    }
    .ui_tpicker_time {
      width: 100%;
    }
    .ui_tpicker_hour,
    .ui_tpicker_minute,
    .ui_tpicker_hour_label,
    .ui_tpicker_minute_label {
      width: 50%;
      float: left;
    }
    .ui_tpicker_hour,
    .ui_tpicker_minute {
      text-align: left;
    }
  }

  select {
    margin: 0;
    &.ui-timepicker-select {
      width: 50%;
    }
  }
  .ui-datepicker-next,
  .ui-datepicker-prev {
    height: 42px !important;
  }
  .ui-state-highlight,
  .ui-widget-content .ui-state-highlight,
  .ui-widget-header .ui-state-highlight {
    color: #fff;
  }
  .ui-state-hover {
    color: #fff;
  }

  .ui-datepicker-title {
    font-size: 13px;
    text-transform: capitalize;
    color: #fff;
    margin: 0;
    border-radius: 0;
    line-height: 26px;
    font-weight: bold;
  }
  .ui-datepicker-header {
    border: 0 !important;
    background: transparent;
    .ui-datepicker-prev {
      left: 1px;
    }
    .ui-datepicker-next {
      right: 1px;
    }
  }
  .ui-datepicker-buttonpane {
    button {
      font-size: 13px;
      color: #fff;
      padding: 0 20px;
      border: none !important;
    }
  }
  .ui-datepicker-prev, .ui-datepicker-next {
    border: 0 !important;
    top: 0;
  }
  .ui-corner-all, .ui-corner-bottom, .ui-corner-right, .ui-corner-br {
    border-radius: 0;
    border: 1px solid $border-color;
  }
  dt {
    font-size: 13px;
    line-height: 26px;
  }

  dd {
    font-size: 18px;
    margin-bottom: 17px;
  }
  th {
    font-weight: 400;
    font-size: 14px;
  }
  td {
    font-size: 13px;
  }
  table {
    border: 0;
    margin-bottom: 0;
    td, tr {
      border-color: #eee;
    }
  }
  .ui-slider-horizontal {
    height: 7px;
    .ui-slider-handle {
      height: 15px;
      width: 15px;
      margin-left: 0;
      border-radius: 50%;
    }
  }

  .ui-timepicker-div {
    > dl {
      text-align: center;
    }
  }
}

.room-search-text-link {
  font-size: 15px;
  font-weight: 500;
  display: inline-block;
  text-align: right;
  p {
    margin-bottom: 5px;
  }
  a {
    color: inherit;
  }
}
