/**
 * Common styles */

// Site content
.site-content {
  padding-top: 51px;
  padding-bottom: 100px;
  &.no-padding {
    padding-top: 0;
    padding-bottom: 0;
  }
}

#wrapper-container {
  overflow: hidden;
}

.content-pusher {
  position: relative;
  left: 0;
  z-index: 99;
  height: 100%;
  @include transition(all 0.5s);
}

.tm-table {
  display: table;
  table-layout: fixed;
}

.table-cell {
  display: table-cell;
  vertical-align: middle;
}

// Social link
.thim-wg-social-link {
  list-style: none;
  margin: 0;
  &:after {
    @extend .clear-after;
  }
  li {
    display: inline-block;
    margin-left: 3px;
    float: left;
    a {
      border: 1px solid #fff;
      height: 60px;
      width: 60px;
      display: inline-block;
      text-align: center;
      line-height: 60px;
      i {
        font-size: 24px;
      }
      &.tripadvisor {
        width: auto;
        padding: 0 20px;
      }
    }
  }
}

// Slider
.thim-link-slider {
  position: relative;
  &:before {
    width: 80px;
    border-top: 2px solid transparent;
    content: "";
    display: inline-block;
    vertical-align: middle;
    position: absolute;
    top: 45%;
    left: 100%;
    margin-left: 20px;
    @include translate(0, -50%);
  }
  &:after {
    width: 80px;
    border-top: 2px solid transparent;
    content: "";
    display: inline-block;
    vertical-align: middle;
    position: absolute;
    top: 45%;
    right: 100%;
    margin-right: 20px;
    @include translate(0, -50%);
  }
  a {
    color: #fff;
    display: inline-block;
  }
}

.tp-bullets.hermes .tp-bullet {
  box-shadow: inset 0 0 0 2px rgba(255, 255, 255, .7);
}

.hermes .tp-bullet:after {
  @include rounded(50%);
}

.vc_row.has-mask {
  &:before {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    opacity: .7;
    z-index: 1;
    display: inline-block;
  }
}

.line-height-normal p {
  line-height: 27px;
}

// Slider style
.flexslider {
  ul {
    margin: 0;
    list-style: none;
  }
  position: relative;
  .flex-direction-nav {
    margin: 0;
    a {
      position: absolute;
      z-index: 9;
      top: 50%;
      cursor: pointer;
      width: 31px;
      margin: 0;
      height: 32px;
      background-color: #fff;
      text-align: center;
      overflow: hidden;
      margin-top: -16px;
      @include transition(all .3s ease-in-out);
      &:hover {
        &:before {
          color: #fff;
        }
      }
    }
    .flex-prev {
      left: -36px;
      opacity: 0;
      &:before {
        content: "\f104";
        font-family: "FontAwesome";
        font-size: 22px;
        line-height: 31px;
        display: inline-block;
      }
    }
    .flex-next {
      right: -36px;
      opacity: 0;
      &:before {
        content: "\f105";
        font-family: "FontAwesome";
        font-size: 22px;
        line-height: 31px;
        display: inline-block;
      }
    }
  }

  &:hover {
    .flex-direction-nav {
      .flex-prev {
        opacity: 0.8;
        left: 5px;
      }
      .flex-next {
        opacity: 0.8;
        right: 5px;
      }
    }
  }
}

// Contact form
.screen-reader-response {
  display: none;
}

.wpcf7-form {
  margin-top: 18px;
  position: relative;
  .contact-page & {
    input,
    textarea {
      @include placeholder(#8A8A8A);
    }
  }

  .wpcf7-not-valid-tip, .wpcf7-validation-errors {
    display: none !important;
  }

  .ajax-loader {
    position: absolute;
    bottom: 15px;
    left: 0;
    right: 0;
    margin: auto !important;
    width: 30px;
  }

  textarea {
    margin-bottom: 23px !important;
  }

  input, select, textarea {
    width: 100%;
    padding: 20px 23px;
    height: auto;
    line-height: normal;
    background: #fff;
    border: 1px solid $border-color;
    @include rounded(0);
    &.wpcf7-not-valid {
      border-color: #a00 !important;
    }
  }

  textarea {
    height: 219px;
  }

  input[type="date"] {
    padding: 2px 15px;
    &::-webkit-inner-spin-button {
      -webkit-appearance: none;
      display: none;
    }
  }
  .wpcf7-submit {

    margin-top: -25px;
    margin-bottom: 0;
    @include transition();
    font-weight: 700;
    height: 60px;
    text-transform: uppercase;
    color: #fff;
    font-size: 14px;
    border: none;
  }
  div.wpcf7-mail-sent-ok {
    margin-left: 0;
    margin-right: 0;
    width: 100%;
    text-align: center;
  }
}

.contact-address {
  margin-top: 18px;
}

.contact-phone {
  line-height: 27px;
  font-size: 18px;
  margin-top: 30px;
}

.thim-font-size-icon .vc_icon_element-icon:before {
  font-size: 24px !important;
}

// Search
body.search {
  .error-404 .search-form {
    max-width: 50%;
  }

  .page-header,
  .page-title {
    margin-top: 0;
  }
  .blog-content {
    background-color: #fff;
    padding: 40px 30px;
    article:last-child {
      margin-bottom: 0 !important;
    }
    &.row {
      margin: 0 0 60px;
    }
  }
}

body.page {
  #comments {
    margin-top: 30px;
  }
}

.line-height-middle {
  .heading_primary {
    line-height: .8;
  }
}

.form-inline {
  &:after {
    clear: both;
    display: block;
    content: "";
  }
}

//MailChimp
.mc4wp-form {
  position: relative;
  max-width: 300px;
  .mc4wp-form-fields {
    input {
      &[type=email],
      &[type=text],
      &[type=number] {
        font-style: italic;
        background-color: #fff;
        border: 0;
        min-width: 300px;
        padding: 13px 20px;
        @include placeholder(#8a8a8a);
      }
    }
    button[type=submit] {
      background: 0 0;
      border: 0;
      position: absolute;
      top: 0;
      right: 0;
      height: 50px;
      width: 50px;
      outline: 0;
      text-align: center;
      line-height: 50px;
      font-size: 0;
      .comingsoon-wrapper & {
        font-size: 13px;
        font-weight: bold;
      }
      .fa {
        font-size: 16px;
        line-height: 50px;
        .comingsoon-wrapper & {
          display: none;
        }
      }
    }
  }
  .mc4wp-response {
    font-size: 13px;
    margin-top: 25px;
    font-style: italic;
  }
}

// Instagram Feed
#sb_instagram {
  .sbi_follow_btn {
    padding-top: 25px;
    a {
      background: transparent;
      border: 0;
      padding: 0;
      font-weight: 700;
      text-transform: uppercase;
      font-size: 13px;
      .fa {
        display: none;
      }
      strong {
        display: inline-block;
      }
    }

  }
}

.style-normal {
  #sb_instagram .sbi_follow_btn {
    a, strong {
      font-weight: 400;
    }
  }
}

// VC
.vc_row {
  &.vc_row-no-padding .vc_column-inner {
    padding-top: 0 !important;
  }
  &.overflow-visible {
    overflow: visible !important;
  }
}

// #main-content
#main-content {
  position: relative;
  z-index: 9;
}

.thim-slider-button {
  padding: 19px 52px 18px !important;
}

.wpb_revslider_element.wpb_content_element {
  margin-bottom: 0;
}

.wpb_gallery .flexslider .flex-direction-nav a:before {
  color: #fff !important;
}

#sb_instagram {
  .sbi_item.sbi_type_image {
    position: relative;
    overflow: hidden;
    .sbi_photo_wrap {
      @include transition(all 1s);
      &:hover {
        @extend .effect-zoom;
      }
    }
  }
}

.wpcf7-mail-sent-ng {
  text-align: center;
}

.thim-list {
  margin: 0;
  list-style: none;
  li {
    padding: 15px 0;
    .fa {
      margin-right: 20px;
    }
  }
}

.thim-readmore {
  font-weight: 700;
  font-size: $font-size-small;
  text-transform: uppercase;
  display: block;
  margin-top: 30px;
  &:before {
    content: "\e900";
    font-family: 'icomoon';
    @include rotate(90deg);
    position: relative;
    display: inline-block;
    margin-right: 10px;
    font-weight: 400;
    width: 10px;
    max-width: 10px;
  }
  &:hover {
    &:before {
      @extend .effect-right;
    }
  }
}

.home-2-position {
  .thim-sc-hb-rooms-search {
    position: absolute !important;
    top: 53%;
    left: 0;
    right: 0;
    margin: auto;
  }
}

body.error404 .main-top .content h1 {
  text-transform: none;
}