// item
.rooms {
  &.tp-hotel-booking {
    .hb_room {
      margin-bottom: 30px;
      .summary {
        border: 1px solid $border-color;
        .media {
          overflow: hidden;
          a {
            &:before {
              display: none;
            }
          }
          img {
            width: 100%;
            @include transition(all 1s);
          }
        }
        .content {
          padding: 34px 30px;
          .title {
            font-size: 24px;
            line-height: 29px;
            margin: 0;
            margin-bottom: 7px;
          }
          .room-types {
            a {
              line-height: 27px;
            }
            .sep {
              margin: 3px 10px;
              width: 4px;
              height: 4px;
              display: inline-block;
              @include border-radius(50%);
              background-color: #ddd;
            }
          }
          .price {
            position: relative;
            right: auto;
            bottom: auto;
            text-align: inherit;
            &:before {
              content: '';
              background: $border-color;
              height: 2px;
              width: 40px;
              display: block;
              margin-top: 23px;
              margin-bottom: 20px;
            }

            span {
              font-weight: bold;
              line-height: 17px;
              text-transform: uppercase;
              display: initial;
              &.title-price {
                padding-right: 5px;
              }

              &.price_value {
                padding-right: 5px;
              }

              &.unit {
                &:before {
                  content: '/';
                  padding-right: 5px;
                }
              }
            }
          }
          .rating {
            position: relative;
            bottom: 20px;
            right: 0;
          }
        }
        &:hover {
          .price {
            @include translate(0, 0);
          }
          .media {
            img {
              @extend .effect-zoom;
            }
          }
        }
      }
    }
  }
}

.rooms-pagination {
  margin-top: 60px;
}