.thim-sc-testimonial {
  text-align: center;
  .block-title {
    position: relative;
    font-size: 18px;
    margin: 0;
    text-transform: uppercase;
    &:after {
      position: absolute;
      content: "";
      width: 60px;
      height: 2px;
      left: 0;
      right: 0;
      bottom: -10px;
      margin: auto;
    }
  }
  .testimonial-item {
    max-width: 620px;
    margin: 0 auto;
    .testimonial-content {
      color: #fff;
      display: inline-block;
      font-size: 24px;
      font-weight: 400;
      margin: 68px 0 30px 0;
      line-height: 28px;
    }

    .testimonial-rating {
      margin: 0;
      &:before {
        content: '\f005\f005\f005\f005\f005';
        font-family: FontAwesome;
        letter-spacing: 4px;
        font-size: 12px;
      }
    }

    .testimonial-name {
      color: #fff;
      text-transform: capitalize;
      margin: 0;
    }
  }
  .owl-controls {
    margin-top: 50px !important;
    .owl-pagination .owl-page {
      span {
        width: 8px;
        height: 8px;
        border: 2px solid #9B9B9B;
        background: none;
        @include opacity(1);
      }
      &.active span {
        background: #fff;
       border-color: #fff;
      }
    }
  }

  &.left {
    position: relative;
    text-align: left;
    padding: 60px;
    max-width: 661px;

    .owl-carousel .owl-wrapper-outer {
      max-width: 85%;
    }
    .block-title {
      padding-left: 70px;
      &:before {
        width: 60px;
        content: "";
        display: inline-block;
        vertical-align: middle;
        position: absolute;
        top: 50%;
        left: 0;
        @include translate(0%, -50%);
      }

      &:after {
        display: none;
      }
    }
    .testimonial-item {
      padding-right: 80px;
      .testimonial-content {
        font-size: 18px;
        margin: 18px 0;
        font-style: italic;
        line-height: 28px;
      }
    }
    .owl-controls {
      margin-top: 0 !important;
      position: absolute;
      right: 0;
      top: 22px;
      .owl-pagination .owl-page {
        display: block;
        margin-bottom: 25px;
        &:last-child {
          margin-bottom: 0;
        }
        span {
          width: 8px;
          height: 8px;
          border: 2px solid #9B9B9B;
          background: none;
          @include opacity(1);
        }
        &.active span {
          background: #fff;
          border-color: #fff;
        }
      }
    }
  }
}

.testimonial-overlay {
  &:after {
    content: '';
    width: 2000px;
    height: 1000px;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: #1B4F8A;
    @include opacity(.51);
  }
}

@media (min-width: 768px) {
  .testi-width.vc_column_container {
    width: 57.66666667%;
  }
}

