.thim-sc-heading {
  &.text-center {
    .heading-des {
      padding-left: 40px;
      padding-right: 40px;
    }
  }
  .article_heading {
    &.no-line {
      display: inline-block;
      .heading_primary {
        padding-left: 0;
        &:before {
          display: none;
        }
      }
    }

    .heading_primary {
      position: relative;
      display: inline-block;
      padding-left: 70px;
      font-weight: bold;
      margin: 0;
      font-size: 18px;
      &:before {
        width: 60px;
        content: "";
        display: inline-block;
        vertical-align: middle;
        position: absolute;
        top: 50%;
        left: 0;
        @include translate(0%, -50%);
      }
    }
    .heading_secondary {
      margin-top: 0;
      font-size: 36px;
      line-height: 120%;
    }
    .heading-des {
      margin-top: 30px;
      line-height: 27px;
    }
    .heading-button {
      margin-top: 35px;
      a {
        @extend .thim-button;
        i {
          @extend .thim-button-icon;
        }
        &:hover {
          i {
            @extend .effect-right;
          }
        }
      }
    }
  }
}